import "../../styles/AdminDash.css";
import "../../styles/CreateProduct.css";
import React, { useEffect, useState } from "react";
import AdminMenu from "../../components/AdminMenu";
import axios from "axios";
import toast from "react-hot-toast";
import { Select } from "antd";
import { useNavigate, useParams } from "react-router-dom";
import { MdDelete } from "react-icons/md";
import { RxUpdate } from "react-icons/rx";

const { Option } = Select;

const UpdateProduct = () => {
  const navigate = useNavigate();
  const params = useParams();

  const [categories, setCategories] = useState([]);
  const [category, setCategory] = useState("");
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [price, setPrice] = useState("");
  const [isFeatured, setIsFeatured] = useState("");
  const [quantity, setQuantity] = useState("");
  const [photo, setPhoto] = useState("");
  const [id, setId] = useState("");

  // get single product
  const getSingleProduct = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/product/get-product/${params.slug}`
      );

      setName(data.product.name)
      setId(data.product._id)
      setDescription(data.product.description)
      setPrice(data.product.price)
      setQuantity(data.product.quantity)
      setIsFeatured(data.product.isFeatured)
      setCategory(data.product.category._id)

    } catch (error) {
      toast.error("Something went wrong");
    }
  };

  useEffect(() => {
    getSingleProduct();
    //eslint-disable-next-line
  },[])

  // get all category
  const getAllCategory = async () => {
    try {
      const { data } = await axios.get(
        `/api/v1/category/allcategory`
      );
      if (data?.success) {
        setCategories(data?.category);
      }
    } catch (error) {
      toast.error("Something went wrong getting category");
    }
  };

  useEffect(() => {
    getAllCategory();
  }, []);

  // update product
  const handleUpdate = async (e) => {
    e.preventDefault();
    try {
      const productData = new FormData();
      productData.append("name", name);
      productData.append("description", description);
      productData.append("category", category);
      productData.append("price", price);
      productData.append("isFeatured", isFeatured);
      productData.append("quantity", quantity);
      photo && productData.append("photo", photo);

      const { data } = await axios.put(
        `/api/v1/product/update-product/${id}`,
        productData
      );
      if (data?.success) {
        toast.success("Product created successfully");
        navigate("/profile/admin/products")
      } else {
        toast.error("Product creation failed");
      }
    } catch (error) {
      toast.error("Something went wrong creating product");
    }
  };

//   delete product
const handleDelete = async () => {
    try {
        const { data } = await axios.delete(`/api/v1/product/delete-product/${id}`)
        toast.success('Product deleted successfully')
        navigate('/profile/admin/products')

    } catch (error) {
        toast.error("Something went wrong deleting product");
    }
}

  return (
    <div className="admin_page">
      <div className="admin_menu">
        <AdminMenu />
      </div>
      <div className="admin_prod_right">
        <h1> Update Product </h1>
        <div className="admin_prod_cont">
            <div className="prod-form">
                <h5>category:</h5>
                <Select
                    placeholder="Select Category"
                    size="large"
                    showSearch
                    onChange={(value) => {
                    setCategory(value);
                    }}
                    value={category}
                >
                    {categories?.map((c) => (
                    <Option key={c._id} value={c._id}>
                        {c.name}
                    </Option>
                    ))}
                </Select>
            </div>
            <div className="prod-form">
                <h5>Image:</h5>
                <div className="photo-btn">
                    <label>
                    {photo ? photo.name : "Select Product Image"}
                    <input
                        type="file"
                        name="photo"
                        accept="image/*"
                        onChange={(e) => setPhoto(e.target.files[0])}
                        hidden
                    />
                    </label>
                </div>
            </div>
          <div className="prod-form">
            <h5>Name:</h5>
            <input
              type="text"
              value={name}
              placeholder="Product Name"
              className="prod-form-input"
              onChange={(e) => setName(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <h5>description:</h5>
            <input
              type="text"
              value={description}
              placeholder="Product Description"
              className="prod-form-input"
              onChange={(e) => setDescription(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <h5>price:</h5>
            <input
              type="number"
              value={price}
              placeholder="Product Price"
              className="prod-form-input"
              onChange={(e) => setPrice(e.target.value)}
            />
          </div>
          <div className="prod-form">
            <h5>isFeatured:</h5>
            <Select
              variant="false"
              placeholder="Select Featured"
              size="large"
              showSearch
              onChange={(value) => {
                setIsFeatured(value);
              }}
              value={isFeatured}
              className="prod-form-select"
            >
              <Option value="0"> No </Option>
              <Option value="1"> Yes </Option>
            </Select>
          </div>
          <div className="prod-form">
            <h5>Quantity:</h5>
            <input
              type="number"
              value={quantity}
              placeholder="Quantity"
              className="prod-form-input"
              onChange={(e) => setQuantity(e.target.value)}
            />
          </div>

          <button className="prod-form-btn update" onClick={handleUpdate}>
            <RxUpdate className="delete-icon" />
            update porduct
          </button>
          <button className="prod-form-btn delete" onClick={handleDelete}>
            <MdDelete className="delete-icon" />
            delete porduct
          </button>
        </div>
      </div>
    </div>
  );
};

export default UpdateProduct;
